.container {
  padding: 0 20px;
}

.header {
  background: $pink;
  width: 100%;
  min-width: 320px;
  height: 502px;
  margin: 0 auto;
}
.header__background {
  background-image: url(../images/header-img-icecream.png), url(../images/header-img-elipse.png);
  background-repeat: no-repeat;
  background-size: 171px, 346px;
  background-position: top 92px left 117px, top 83px left 36px;
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
  height: 502px;
}
.header__container {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  height: 64px;
}
.header__logo {
  display: flex;
  width: 199px;
  height: 50px;
}
.header__svg {
  position: absolute;
  display: flex;
  align-items: center;
  cursor: pointer;
  right: 20px;
  top: 25px;
}

.nav__list {
  display: flex;
  display: none;
  flex-direction: row;
  list-style: none;
  gap: 35px;
  padding: 0px;
}
.nav__list-item {
  font-family: $standard-font;
  font-weight: map-get($font-weight, bold);
  font-size: map-get($font-size, standard);
  font-style: normal;
  line-height: 21px;
  color: $white;
  cursor: pointer;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  :focus {
    color: #ecebeb;
  }
}
.nav__list-color {
  color: $red-1;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  :focus {
    color: $red-3;
  }
}
.header__button {
  display: none;
}
.header__button-item {
  margin-left: 110px;
  background: #ffffff;
  border-radius: 22px;
  font-family: $standard-font;
  font-style: normal;
  font-weight: map-get($font-weight, bold);
  font-size: map-get($font-size, enlarged);
  line-height: 21px;
  display: flex;
  padding: 10px 37px;
  border: none;
  cursor: pointer;
  color: $red-1;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  :focus {
    background-color: $button-hover;
  }
}
.hero {
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: space-between;
  margin: 0 auto;
  width: 100%;
  height: auto;
  max-width: 480px;
}
.hero__container {
  // position: relative;
  display: flex;
  flex-direction: column;
}
.hero__title {
  font-family: $header-font;
  font-style: normal;
  font-weight: map-get($font-weight, regular);
  font-size: map-get($font-size, title-section-header);
  line-height: 30px;
  letter-spacing: map-get($letter-spacing, standard);
  text-transform: uppercase;
  color: $white;
  width: 218px;
  height: 106px;
  padding: 0px;
  margin-bottom: 0px;
  margin-top: 137px;
}
.hero__title-color {
  color: $red-1;
  width: 100%;
  height: auto;
  font-size: map-get($font-size, title2-section-header);
}
.hero__buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  display: flex;
  flex-direction: row;
  gap: 15px;
  height: 40px;
}
.hero__button {
  padding: 10px 16px;
  background: #d41443;
  border-radius: 20px;
  border: none;
  font-family: $standard-font;
  font-style: normal;
  font-weight: map-get($font-weight, regular);
  font-size: map-get($font-size, the-smallest);
  line-height: 10px;
  color: $white;
  cursor: pointer;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  :focus {
    background-color: $red-3;
  }
}
.hero__button-color {
  color: $red-1;
  background-color: $white;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  :focus {
    background-color: $button-hover;
  }
}
.hero__label {
  display: none;
  margin-top: 112px;
  margin-left: 295px;
  font-family: $standard-font;
  font-style: normal;
  font-weight: map-get($font-weight, regular);
  font-size: map-get($font-size, standard);
  line-height: 140%;
  color: $white;
  width: 200px;
  height: 78px;
}

.hero__item {
  display: flex;
  display: none;
  flex-direction: column;
  align-content: space-between;
  margin-top: 68px;
  padding: 0px;
  margin-right: 115px;
}

.hero__item-value {
  font-family: $header-font;
  font-style: normal;
  font-weight: map-get($font-weight, regular);
  font-size: map-get($font-size, hero-numbers);
  line-height: 53px;
  text-align: right;
  letter-spacing: map-get($letter-spacing, standard);
  text-transform: uppercase;
  margin: 0px;
  padding: 0px;
  color: $red-1;
}
.hero__item-name {
  font-family: $standard-font;
  font-style: normal;
  font-weight: map-get($font-weight, regular);
  font-size: map-get($font-size, standard);
  line-height: 18px;
  text-align: right;
  color: $white;
  width: 80px;
}
// okno modalne
.js-menu-container {
  position: fixed;
  align-content: center;
  justify-content: center;
  right: 0px;
  top: 0px;
  width: 250px;
  height: 502px;
  transform: translateX(3000px);
  transition: transform 0.5s;
  z-index: 100;
  background-color: $modal-color;
}

.js-close-menu {
  cursor: pointer;
}

.is-open {
  display: block;
  transform: translateX(0%);
}
.mobile__menu-svg {
  position: absolute;
  top: 23px;
  left: 208px;
}
.mobile__menu-button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: $standard-font;
  font-style: normal;
  font-weight: map-get($font-weight, regular);
  font-size: map-get($font-size, enlarged);
  line-height: 21px;
  border: none;
  color: $red-1;
  width: 209px;
  height: 44px;
  background: #ffffff;
  border-radius: 22px;
  margin-right: 0;
  margin-top: 60px;
  margin-bottom: 19px;
  cursor: pointer;
}
.mobile__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  list-style: none;
  padding: 0px;
}
.mobile__list-item {
  font-family: $standard-font;
  font-style: normal;
  font-weight: map-get($font-weight, bold);
  font-size: map-get($font-size, standard);
  line-height: 18px;
  display: flex;
  align-items: center;
  color: $white;
  padding-top: 13px;
  padding-bottom: 13px;
  border-top: 1px solid #e18298;
  border-bottom: 1px solid #e18298;
}

.mobile__list-item > a {
  color: $white;

  &:hover,
  &:focus {
    color: $red-1;
  }
}

.mobile__menu-container {
  width: 100%;
  min-width: 250px;
  padding: 0px 23px;
}
