.products {
  &__container {
    margin: 0 auto;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__header {
    display: flex;
    flex-direction: column;
    position: relative;
    justify-content: flex-end;
    margin-top: 157px;
    height: 36px;

    font-family: $header-font;
    font-style: normal;
    font-weight: map-get($font-weight, regular);
    font-size: map-get($font-size, products-header);
    line-height: 1.147;
    text-align: center;
    letter-spacing: map-get($letter-spacing, standard);
    text-transform: uppercase;
    color: $red-1;
  }

  &__header::before {
    display: flex;
    justify-content: center;
    content: '100% natural';
    margin-bottom: 15px;
    font-family: $header-font;
    font-style: normal;
    font-weight: map-get($font-weight, regular);
    font-size: map-get($font-size, products-before);
    line-height: 1.167;
    text-transform: uppercase;
    color: $dark-pink;
  }
}

.products__list {
  margin-top: 144px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  list-style: none;
  gap: 130px;
}

.products__list:last-child {
  margin-bottom: 98px;
}

.products__overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 0;
  width: 100%;
  background-color: $modal-color;
  border-radius: 24px;
  overflow: auto;
  z-index: 99;
  text-align: center;
  padding-top: 0;
  transition: width 0.5s;
  opacity: 0.85;
}

.overlayshow {
  padding-top: 100px;
  transition: width 0.5s, padding-top 0.5s;
  height: 495px;
}

.products__button {
  position: relative;
  z-index: 100;
}

.products__card {
  display: flex;
  flex-direction: column;
  position: relative;
  justify-content: center;
  width: 280px;
  margin: 0 auto;
  height: 485px;
  background: $pink;
  border-radius: 24px;
  &::before {
    content: '';
    position: absolute;
    display: block;
    width: 249px;
    height: 252px;
    top: -113px;
    left: 16px;

    background-image: url(../images/icecream-mobile.png);
    background-repeat: no-repeat;
    background-size: contain;
    z-index: 2;
  }
  &--image-vanilla {
    background: $yellow;
  }
  &--image-vanilla::before {
    content: '';
    position: absolute;
    display: block;
    width: 249px;
    height: 252px;
    top: -113px;

    background-image: url(../images/icecoffee-mobile.png);
    z-index: 2;
  }
  &--image-chocolate {
    background: $green;
  }

  &--image-chocolate::before {
    content: '';
    position: absolute;
    display: block;
    width: 249px;
    height: 252px;
    top: -113px;

    background-image: url(../images/milkshakes-mobile.png);
    z-index: 2;
  }
}

.products__title {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 174px;
  height: 23px;
  margin: 161px 53px 66px;

  font-family: $header-font;
  font-size: map-get($font-size, review-signature);
  font-style: normal;
  font-weight: map-get($font-weight, regular);
  line-height: 1.15;

  text-align: center;
  letter-spacing: map-get($letter-spacing, standard);
  text-transform: uppercase;

  color: $white;

  &::after {
    content: '';
    display: flex;
    position: absolute;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: $white;
    top: 53px;
    left: 50%;
    transform: translateX(-50%);
    box-shadow: 10px 0px 0px $white, -10px 0px 0px $white;
  }

  &--overlay {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 53px 25px;
    font-family: $header-font;
    font-size: map-get($font-size, review-signature);
    font-weight: map-get($font-weight, regular);
    text-transform: uppercase;
    color: $white;
    letter-spacing: map-get($letter-spacing, links);
  }
}

.products__content {
  margin: 25px 20px;
  font-family: $standard-font;
  font-style: normal;
  font-weight: map-get($font-weight, bold);
  font-size: map-get($font-size, standard);
  line-height: 1.52;
  text-align: center;
  letter-spacing: map-get($letter-spacing, standard);
  color: $white;
}

.products__field {
  position: relative;
  display: flex;
  width: 200px;
  height: 124px;
  margin-left: 50%;
  transform: translateX(-50%);

  font-family: $standard-font;
  font-style: normal;
  font-weight: map-get($font-weight, bold);
  font-size: map-get($font-size, standard);
  line-height: 1.52;

  text-align: center;
  letter-spacing: map-get($letter-spacing, standard);

  color: $white;
  margin-bottom: 30px;
}

.products__button {
  display: flex;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background-image: url(../images/arrow-right.png);
  background-repeat: no-repeat;
  background-size: contain;
  margin-bottom: 40px;
  margin-left: 120px;
  cursor: pointer;
}

@media screen and (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .products__card::before {
    background-image: url(../images/icecream-mobile@2x.png);
  }
  .products__card--image-vanilla::before {
    background-image: url(../images/icecoffee-mobile@2x.png);
  }
  .products__card--image-chocolate::before {
    background-image: url(../images/milkshakes-mobile@2x.png);
  }

  .products__button {
    background-image: url(../images/arrow-right@2x.png);
  }
}
